import { BrowserModule } from '@angular/platform-browser';
import { HttpModule } from '@angular/http';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';

import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { YoutubeApiProvider } from '../providers/youtube-api-service/youtube-api-service';
import { YoutubeVideoPlayer } from '@ionic-native/youtube-video-player';

@NgModule({
    declarations: [
        MyApp,
        HomePage
    ],
    imports: [
        BrowserModule,
        IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/youtube-playlist/youtube-playlist.module#YoutubePlaylistPageModule', name: 'YoutubePlaylistPage', segment: 'youtube-playlist', priority: 'low', defaultHistory: [] }
  ]
}),
        HttpModule
    ],
    bootstrap: [IonicApp],
    entryComponents: [
        MyApp,
        HomePage
    ],
    providers: [
        StatusBar,
        SplashScreen,
        {provide: ErrorHandler, useClass: IonicErrorHandler},
        YoutubeApiProvider,
        YoutubeVideoPlayer
    ]
})
export class AppModule {}



